import "../../style/navigate-print.css";
import React, { useEffect, useRef, useState } from "react";
import { IconButton } from "@mui/material";
import LocalPrintshopIcon from "@mui/icons-material/LocalPrintshop";
import { arrangeData } from "../../components/PrintPreview/dataCore";
import { format } from "date-fns";
let scaleValue = 100;

export default function NavigatePrint() {
  const [row, setRows] = useState<Array<any>>([]);
  const notTable = useRef(localStorage.getItem("printString") as string);
  const column = useRef(
    JSON.parse(localStorage.getItem("column") as string) as Array<any>
  );
  const title = useRef(localStorage.getItem("title") as string);
  const state = useRef(JSON.parse(localStorage.getItem("state") as string));
  const module = useRef(localStorage.getItem("module") as string);

  useEffect(() => {
    if (notTable.current) {
      const onePage = document.querySelector(".one-page") as Element;
      onePage.innerHTML = notTable.current;
    } else {
      const rows = JSON.parse(localStorage.getItem("dataString") as string);
      arrangeData({
        adjustMaxHeight: 500,
        beforeArrangeData(itm) {
          itm.Check_Date = `${itm.Check_Date} - ${format(new Date(itm.Check_Date), 'eee')}`;
          return itm;
        },
        column: column.current,
        data: rows,
        fontSize: "11px",
      }).then((data) => {
        setRows(data);
      });
    }
  }, []);
  function convertElementToURL(page: Element) {
    const elements = page.querySelector("table") as Element;
    console.log(elements);
    const elementString = new XMLSerializer().serializeToString(elements);
    const height = elements.getBoundingClientRect().height;
    const width = elements.getBoundingClientRect().width;
    const viewBox = `0 0 ${width} ${height}`;
    let scale = 0;

    if (scaleValue < 100) {
      scale = 100 + (100 - scaleValue);
    } else if (scaleValue > 100) {
      scale = 100 - (scaleValue - 100);
    } else {
      scale = 100;
    }

    const dataURL =
      "data:image/svg+xml," +
      encodeURIComponent(
        `<svg xmlns="http://www.w3.org/2000/svg"  width="${scale}%" height="${scale}%">
            <foreignObject viewBox="${viewBox}" width="100%" height="100%">
                ${elementString}
            </foreignObject>
            <style>
                .page{
                    page-break-after: always;
                    display: flex;
                    flex-direction: column;
                    height: 100vh;
                    width: 100vw;
                    box-shadow: 1px 1px 1px black;
                }
                .content{
                    flex: 1;
                    block-size: fit-content;
                    text-align: center;
                }
                table {
                  border-collapse: collapse;
                  border-spacing: 50px
                }
                thead tr.headerColumn{
                    border-bottom: 1px solid black;
                }
                .content img{
                    width: 97%;
                    height: 100%;
                }
                .footer, .header{
                    padding-left: 50px;
                    padding-right: 50px;
                }
                td, th{
                font-family: 'Lato', sans-serif;
                font-weight: 700;
                padding: 0px 5px;
              }
           
            </style> 
          </svg>`
      );
    return dataURL;
  }
  const captureElement = async (page: Element) => {
    const imgData = convertElementToURL(page);
    const tableIamge = '<img src="' + imgData + '" />';
    const content = `<div class="content">${tableIamge}</div>`;
    const footer = page.querySelector(".footer") as Element;

    return `<div class="page"> ${content}${footer.outerHTML}</div>`;
  };

  async function getPaperToPrint() {
    let printString = "";
    const pages = document.querySelectorAll(".page");
    for (const page of pages) {
      printString += await captureElement(page);
    }
    return printString;
  }
  return (
    <main
      className="main"
      style={{
        background: "#e4e4e7",
        width: "100%",
        height: "auto",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        padding: "50px 0 ",
        boxSizing: "border-box",
        margin: "0",
      }}
    >
      <header
        style={{
          position: "fixed",
          top: "0",
          left: "0",
          right: "0",
          height: "40px",
          borderBottom: "1px solid #f1f5f9",
          background: "#e4e4e7",
          boxShadow: "10px 10px 34px -6px rgba(0,0,0,0.44)",
          display: "flex",
          justifyContent: "flex-end",
          columnGap: "10px",
          padding: "0 50px",
        }}
      >
        <IconButton
          onClick={async () => {
            const iframe = document.createElement(
              "iframe"
            ) as HTMLIFrameElement;
            iframe.style.display = "none";
            document.body.appendChild(iframe);
            const iframeDocument =
              iframe.contentDocument || iframe.contentWindow?.document;
            if (notTable.current !== null) {
              const page = document.querySelector(".page") as Element;
              const htmlContent = `
                <!DOCTYPE html>
                <html lang="en">
                <head>
                    <meta charset="UTF-8">
                    <meta name="viewport" content="width=device-width, initial-scale=1.0">
                    <title>Printed HTML Content</title>
                    <style>
                    @import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&family=Plus+Jakarta+Sans:ital,wght@0,200..800;1,200..800&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');
                    @media print {
                      body {
                            margin: 0;
                            padding: 0;
                        }
                      .page{
                          page-break-after: always;
                          display: flex;
                          flex-direction: column;
                          height: 100vh;
                          width: 100vw;
                          box-shadow:none !important;
                          font-family: "Roboto", sans-serif;
                      }
                    }
                    </style>
                </head>
                <body>
                      ${page.outerHTML}
                </body>
                </html>
            `;
              iframeDocument?.open();
              iframeDocument?.write(htmlContent);
              iframeDocument?.close();
              setTimeout(function () {
                iframe.contentWindow?.print();
                iframe.parentNode?.removeChild(iframe);
              }, 500);
              return;
            } else {
              getPaperToPrint().then((printString) => {
                const htmlContent = `
                <!DOCTYPE html>
                <html lang="en">
                <head>
                    <meta charset="UTF-8">
                    <meta name="viewport" content="width=device-width, initial-scale=1.0">
                    <title>Printed HTML Content</title>
                    <style>
                    @import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&family=Plus+Jakarta+Sans:ital,wght@0,200..800;1,200..800&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');
                    @media print {
                      @page{
                          margin-top: 0mm;
                          margin-bottom: 0mm;
                          margin-left: 0mm;
                          margin-right: 0mm;
                      }
                      body {
                            margin: 0;
                            padding: 0;
                        }
                      .page{
                          page-break-after: always;
                          display: flex;
                          flex-direction: column;
                          height: 100vh;
                          width: 100vw;
                          box-shadow: none;
                      }
                      .content{
                          flex: 1;
                          block-size: fit-content;
                          text-align: center;
                      }
                      .content img{
                          width: 95%;
                          height: 100%;
                      }
                      .footer, .header{
                        padding-left: 50px;
                        padding-right: 50px;
                      }
                      
                        .page table td,.page table th{
                          font-family: "Roboto", sans-serif;
                          font-weight: 100;
                          font-style: normal;
                      }
                    }

                    </style>
                </head>
                <body>
                      ${printString}
                </body>
                </html>
            `;
                console.log(htmlContent);

                iframeDocument?.open();
                iframeDocument?.write(htmlContent);
                iframeDocument?.close();
                setTimeout(function () {
                  iframe.contentWindow?.print();
                  iframe.parentNode?.removeChild(iframe);
                }, 500);
              });
            }
          }}
        >
          <LocalPrintshopIcon color="primary" />
        </IconButton>
      </header>
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          flexDirection: "column",
          rowGap: "10px",
        }}
      >
        {module.current === "pdc" ? (
          row.length <= 0 ? (
            <div
              className="page content one-page"
              style={{
                background: "white",
                boxShadow: "10px 10px 34px -6px rgba(0,0,0,0.44)",
                width: localStorage.getItem("paper-width") as string,
                height: localStorage.getItem("paper-height") as string,
              }}
            ></div>
          ) : (
            row?.map((pages, RowNumber) => {
              return (
                <div
                  className="page content"
                  style={{
                    display: "flex",
                    flexDirection: "column",
                    background: "white",
                    boxShadow: "10px 10px 34px -6px rgba(0,0,0,0.44)",
                    width: localStorage.getItem("paper-width") as string,
                    height: localStorage.getItem("paper-height") as string,
                  }}
                  key={RowNumber}
                >
                  <div
                    style={{
                      flex: 1,
                    }}
                  >
                    {
                      <table
                        style={{ width: "100%", borderCollapse: "collapse" }}
                      >
                        <thead>
                          {RowNumber === 0 &&
                            title.current
                              .split("\n")
                              .map((t: string, idx: number) => {
                                return (
                                  <tr key={idx}>
                                    <th
                                      style={{
                                        fontSize: "14px",
                                        fontWeight: "bold",
                                        textAlign: "center",
                                      }}
                                      colSpan={column.current.length}
                                    >
                                      {t}
                                    </th>
                                  </tr>
                                );
                              })}
                        </thead>
                        <tbody>
                          <tr style={{ height: "40px" }}></tr>
                          {RowNumber === 0 && (
                            <>
                              <tr>
                                <td
                                  colSpan={1}
                                  style={{
                                    textAlign: "right",
                                    fontSize: "11px",
                                    fontWeight: "bold",
                                  }}
                                >
                                  P.N. No. :
                                </td>
                                <td
                                  colSpan={2}
                                  style={{
                                    fontSize: "11px",
                                    fontWeight: "bold",
                                  }}
                                >
                                  {state.current.PNo}
                                </td>
                                <td
                                  colSpan={1}
                                  style={{
                                    textAlign: "right",
                                    fontSize: "11px",
                                    fontWeight: "bold",
                                  }}
                                >
                                  Reference No :
                                </td>
                                <td
                                  colSpan={1}
                                  style={{
                                    fontSize: "11px",
                                    fontWeight: "bold",
                                  }}
                                >
                                  {state.current.Ref_No}
                                </td>
                              </tr>
                              <tr>
                                <td
                                  colSpan={1}
                                  style={{
                                    textAlign: "right",
                                    fontSize: "11px",
                                    fontWeight: "bold",
                                  }}
                                >
                                  Client Name :
                                </td>
                                <td
                                  colSpan={2}
                                  style={{
                                    fontSize: "11px",
                                    fontWeight: "bold",
                                  }}
                                >
                                  {state.current.Name} ({state.current.IDNo})
                                </td>
                                <td
                                  colSpan={1}
                                  style={{
                                    textAlign: "right",
                                    fontSize: "11px",
                                    fontWeight: "bold",
                                  }}
                                >
                                  Date Received :
                                </td>
                                <td
                                  colSpan={1}
                                  style={{
                                    fontSize: "11px",
                                    fontWeight: "bold",
                                  }}
                                >
                                  {format(
                                    new Date(state.current.Date),
                                    "MM/dd/Y"
                                  )}
                                </td>
                              </tr>
                              <tr>
                                <td
                                  colSpan={1}
                                  style={{
                                    textAlign: "right",
                                    fontSize: "11px",
                                    fontWeight: "bold",
                                  }}
                                >
                                  Remarks :
                                </td>
                                <td
                                  colSpan={4}
                                  style={{
                                    fontSize: "11px",
                                    fontWeight: "bold",
                                  }}
                                >
                                  {state.current.Remarks}
                                </td>
                              </tr>
                            </>
                          )}
                          <tr style={{ height: "40px" }}></tr>
                          <tr style={{ borderBottom: "1px solid black" }}>
                            {column.current.map((itm: any, rowIdx: number) => {
                              return (
                                <th
                                  style={{
                                    width: itm.width,
                                    fontSize: "12px",
                                    fontWeight: "bold",
                                    textAlign: "left",
                                  }}
                                  key={rowIdx}
                                >
                                  {itm.header}
                                </th>
                              );
                            })}
                          </tr>
                          <tr style={{ height: "5px" }}></tr>
                          {pages.map((rowItem: any, rowIdx: number) => {
                            return (
                              <tr key={rowIdx}>
                                {column.current.map(
                                  (colItem: any, colIdx: number) => {
                                    return (
                                      <td
                                        className={`editable not-looking  `}
                                        key={colIdx}
                                        style={{
                                          fontSize: "11px",
                                          fontWeight: "500",
                                          paddingLeft: "50px !important",
                                          width: `${colItem.width} !important`,
                                          textAlign:
                                            colItem.datakey === "Check_Amnt" ||
                                            colItem.datakey === "SEQ"
                                              ? "right"
                                              : "left",
                                        }}
                                      >
                                        {rowItem[colItem.datakey]}
                                      </td>
                                    );
                                  }
                                )}
                              </tr>
                            );
                          })}
                        </tbody>
                      </table>
                    }
                  </div>
                  <div
                    className="footer"
                    style={{
                      height: "100px",
                      display: "flex",
                      justifyContent: "space-between",
                      columnGap: "20px",
                      flexDirection: "column",
                    }}
                  >
                    <div
                      style={{
                        height: "100px",
                        display: "flex",
                        justifyContent: "space-between",
                        columnGap: "20px",
                      }}
                    >
                      <p
                        style={{
                          fontSize: "10px",
                          display: "flex",
                          width: "100%",
                          alignItems: "center",
                          columnGap: "10px",
                        }}
                      >
                        <span style={{ fontWeight: "bold" }}>Prepared :</span>
                        <span
                          style={{
                            borderBottom: "1px solid black",
                            flex: 1,
                            height: "11px",
                          }}
                        ></span>
                      </p>
                      <p
                        style={{
                          fontSize: "10px",
                          display: "flex",
                          width: "100%",
                          alignItems: "center",
                          columnGap: "10px",
                        }}
                      >
                        <span style={{ fontWeight: "bold" }}>Checked :</span>
                        <span
                          style={{
                            borderBottom: "1px solid black",
                            flex: 1,
                            height: "11px",
                          }}
                        ></span>
                      </p>
                      <p
                        style={{
                          fontSize: "10px",
                          display: "flex",
                          width: "100%",
                          alignItems: "center",
                          columnGap: "10px",
                        }}
                      >
                        <span style={{ fontWeight: "bold" }}>Approved :</span>
                        <span
                          style={{
                            borderBottom: "1px solid black",
                            flex: 1,
                            height: "11px",
                          }}
                        ></span>
                      </p>
                    </div>
                    <div
                      style={{
                        height: "30px",
                        display: "flex",
                        justifyContent: "space-between",
                      }}
                    >
                      <p style={{ fontSize: "8px" }}>
                        {format(new Date(), "MM/dd/Y")}
                      </p>
                      <p style={{ fontSize: "8px" }}>
                        Page {RowNumber + 1} of {row.length}
                      </p>
                    </div>
                  </div>
                </div>
              );
            })
          )
        ) : (
          <div
            className="page content one-page"
            style={{
              background: "white",
              boxShadow: "10px 10px 34px -6px rgba(0,0,0,0.44)",
              width: localStorage.getItem("paper-width") as string,
              height: localStorage.getItem("paper-height") as string,
            }}
          ></div>
        )}
      </div>
    </main>
  );
}
